export default {
	name: 'customTabbar',
	data() {
		return {
			selected: 0,
			color: '#7A7E83',
			selectedColor: '#3cc51f',
			list: [
				{
					pagePath: 'pages/tabbarTest/tabbarTest',
					iconPath: 'static/icon/star.png',
					selectedIconPath: 'static/icon/star.png',
					text: '测试'
				},
				{
					pagePath: 'pages/index/index',
					iconPath: 'static/icon/star.png',
					selectedIconPath: 'static/icon/star.png',
					text: '主页'
				}
			]
		};
	},
	attached() {},
	methods: {
		switchTab(e) {
			const data = e.currentTarget.dataset;
			const url = data.pagepath;
			uni.switchTab({ url });
			this.setData({
				selected: data.index
			});
		}
	}
};
